import React from "react";
function reloadDO(x){
  if (x=='r'){
    window.location.reload()
  }

}
const Popup = props => {
  return (
    <div className="popup-box">
      <div className="box">
        <span className="close-icon" onClick={reloadDO('r')}>x</span>
        {props.content}
      </div>
    </div>
  );
};

export default Popup;
